<template>
    <div>
        <top-nav title="用户列表" ></top-nav>
        <div class="wyu-tips wyu-tips-warning">
            <p>1).这里是用户列表</p>
        </div>
        <div class="wyu-main">
            <div class="wyu-block" v-loading="loading">
                <el-form :inline="true" :model="search" class="search-form" size="mini">
                    <el-form-item label="关键字">
                        <el-input v-model="search.keyword" placeholder="手机或者昵称"></el-input>
                    </el-form-item>
                    <el-form-item label="会员组">
                        <el-select v-model="search.vipGroup" placeholder="选择会员组">
                            <el-option label="全部状态" :value="0"></el-option>
                            <el-option label="注册会员" :value="1"></el-option>
                            <el-option label="PLUS会员" :value="2"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="开始时间">
                        <el-date-picker
                                v-model="search.bgTime"
                                type="datetime"
                                placeholder="选择日期时间">
                        </el-date-picker>
                    </el-form-item>
                    <el-form-item label="结束时间">
                        <el-date-picker
                                v-model="search.endTime"
                                type="datetime"
                                placeholder="选择日期时间">
                        </el-date-picker>
                    </el-form-item>
                    <el-form-item label="用户状态">
                        <el-select v-model="search.is_lock" placeholder="选择状态">
                            <el-option label="全部状态" :value="0"></el-option>
                            <el-option label="锁定" :value="2"></el-option>
                            <el-option label="正常" :value="1"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="注册设备">
                        <el-select v-model="search.device" placeholder="选择状态">
                            <el-option label="全部设备" :value="0"></el-option>
                            <el-option label="微信小程序" value="weixin"></el-option>
                            <el-option label="支付宝小程序" value="alipay"></el-option>
                            <el-option label="百度小程序" value="baidu"></el-option>
                            <el-option label="头条小程序" value="toutiao"></el-option>
                            <el-option label="H5" value="h5"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="最近登录设备">
                        <el-select v-model="search.last_device" placeholder="选择状态">
                            <el-option label="全部设备" :value="0"></el-option>
                            <el-option label="微信小程序" value="weixin"></el-option>
                            <el-option label="支付宝小程序" value="alipay"></el-option>
                            <el-option label="百度小程序" value="baidu"></el-option>
                            <el-option label="头条小程序" value="toutiao"></el-option>
                            <el-option label="H5" value="h5"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item>
                        <el-button size="mini" @click="seachDatas" type="primary">搜索</el-button>&nbsp;
                        <el-button size="small" @click="search.name='',search.vipGroup=0,search.bgTime='',
                    search.endTime='',search.is_lock=0,search.device='',search.last_device='',seachDatas()" type="text">取消搜索
                        </el-button>
                    </el-form-item>
                </el-form>
                <el-table :data="userList" style="width: 100%" size="mini">
                    <el-table-column width="100" prop="user_id" label="用户ID"></el-table-column>
                    <el-table-column label="头像" width="100">
                        <template slot-scope="scope">
                            <img width="50" :src="scope.row.face"/>
                        </template>
                    </el-table-column>
                    <el-table-column prop="nickname" label="用户昵称"></el-table-column>
                    <el-table-column prop="mobile" label="手机号"></el-table-column>
                    <el-table-column label="会员组" width="260">
                        <template slot-scope="scope">
                            <p>会员组: {{scope.row.vip > 0 ? "PLUS会员" : '注册会员'}}</p>
                            <p v-if="scope.row.vip > 0">会员到期: <span :style="scope.row.vip_tips == 1? 'color: red' : ''">{{scope.row.vip_time}}</span>
                            </p>
                        </template>
                    </el-table-column>
                    <el-table-column label="明细">
                        <template slot-scope="scope">
                            <p>余额：<span style="color: red;">￥{{scope.row.money}}</span></p>
                            <p>订单数：<span>{{scope.row.order_num}}</span></p>
                        </template>
                    </el-table-column>
                    <el-table-column width="200" label="注册日期">
                        <template slot-scope="scope">
                            <p>时间：{{scope.row.reg_time}}</p>
                            <p>设备：{{scope.row.device}}</p>
                        </template>
                    </el-table-column>
                    <el-table-column width="200" label="最后登录">
                        <template slot-scope="scope">
                            <p>时间：{{scope.row.last_time}}</p>
                            <p>设备：{{scope.row.last_device}}</p>
                        </template>
                    </el-table-column>
                    <el-table-column label="状态">
                        <template slot-scope="scope">
                            <p v-if="scope.row.is_lock ==1" style="color:red;">冻结中</p>
                            <p v-if="scope.row.is_lock ==0">正常</p>
                        </template>
                    </el-table-column>
                    <el-table-column label="操作">
                        <template slot-scope="scope">
                            <div class="list-table-action">
                                <router-link target="_blank" :to="'/customer/view/'+scope.row.user_id">
                                    <el-button type="text">跟踪</el-button>
                                </router-link>
                            </div>
                        </template>
                    </el-table-column>
                </el-table>
                <div style="text-align: right" class="block">
                    <el-pagination @current-change="setPage" :page-size="limit"
                                   layout="total, prev, pager, next" :total="count">
                    </el-pagination>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    import * as api from '@/api.js';
    import TopNav from "@/components/TopNav";

    export default {
        name: "customerIndex",
        components: {TopNav},
        data: function () {
            return {
                search: {
                    keyword: '',
                    is_lock: 0,
                    bgTime: '',
                    endTime: '',
                    vipGroup: 0,
                    device: '',
                    last_device: '',
                },
                page: 1,
                limit: 10,
                count: 0,
                loading: false,
                datas: [],
                userList: [],
            };
        },
        created() {
            this.getUserList();
        },
        methods: {
            setPage(res) {
                this.page = res;
                this.getUserList();
            },
            seachDatas() {
                this.page = 1;
                this.getUserList();
            },
            getUserList() {
                var that = this;
                that.loading = true;
                api.post("/shop/admin.user.user/getUserList", {
                    appToken: that.$store.getters.appToken,
                    keyword: that.search.keyword,
                    is_lock: that.search.is_lock,
                    bgTime: that.search.bgTime,
                    endTime: that.search.endTime,
                    vipGroup: that.search.vipGroup,
                    device: that.search.device,
                    last_device: that.search.last_device,
                    page: this.page,
                }).then(res => {
                    that.userList = res.list;
                    that.count = res.count;
                    that.limit = res.limit;
                    that.loading = false;
                }).catch(() => {
                    that.loading = false;
                });
            },

        }
    };
</script>
